.minesweeper-border-colors-inset {
  border-left-color: #808080;
  border-top-color: #808080;
  border-bottom-color: white;
  border-right-color: white;
}

.minesweeper-border-colors-outset {
  border-left-color: white;
  border-top-color: white;
  border-right-color: #808080;
  border-bottom-color: #808080;
}

.minesweeper-font {
  font-family: 'Sigmar One', cursive;
}

.menu-bar-gradient {
  /* bg-[#3B77BC] */
  $base-color: #0855dd;
  background: $base-color;
  background: linear-gradient(
    0deg,
    darken($base-color, 50%) 0%,
    darken($base-color, 10%) 3%,
    $base-color 20%,
    $base-color 80%,
    lighten($base-color, 15%) 97%,
    lighten($base-color, 20%) 100%
  );
}
.vertical-xp-gradient {
  $base-color: #0855dd;
  background: $base-color;
  background: linear-gradient(
    0deg,
    $base-color 0%,
    $base-color 80%,
    lighten($base-color, 15%) 97%,
    lighten($base-color, 20%) 100%
  );
}
